import { Component, OnInit, AfterContentInit } from '@angular/core';
import * as $ from 'jquery';

@Component({
  selector: 'comp-portifolio',
  templateUrl: './portifolio.component.html',
  styleUrls: ['./portifolio.component.scss']
})
export class PortifolioComponent implements OnInit, AfterContentInit {


  constructor() { }

  ngOnInit() {
  }

  ngAfterContentInit(): void {
    this.preloader();
  }

  preloader(): void {
    setTimeout(() => {
      $('#preloader').fadeOut('slow', () => { $(this).remove(); });
    }, 3000);
  }

}
