import { Component, OnInit, AfterContentInit } from '@angular/core';

@Component({
  selector: 'comp-portifolio-detalhe',
  templateUrl: './portifolio-detalhe.component.html',
  styleUrls: ['./portifolio-detalhe.component.scss']
})
export class PortifolioDetalheComponent implements OnInit, AfterContentInit {

  constructor() { }

  ngOnInit() {
  }

  ngAfterContentInit(): void {
    this.preloader();
  }

  preloader(): void {
    setTimeout(() => {
      $('#preloader').fadeOut('slow', () => { $(this).remove(); });
    }, 3000);
  }

}
